<template>
	  <view class="param-info" v-if="Object.keys(paramInfo).length !== 0">
	    <text>参数信息</text>
	    <table :key="index" class="info-size" v-for="(table, index) in paramInfo.sizes">
	      <tr :key="indey" v-for="(tr, indey) in table">
	        <td :key="indez" v-for="(td, indez) in tr">{{ td }}</td>
	      </tr>
	    </table>
	
	    <table class="info-param">
	      <tr :key="index" v-for="(info, index) in paramInfo.infos">
	        <td class="info-param-key">{{ info.key }}</td>
	        <td class="param-value">{{ info.value }}</td>
	      </tr>
	    </table>
	
	    <view class="info-image" v-if="paramInfo.image.length !== 0">
	      <image :src="paramInfo.image" alt="" />
	    </view>
	  </view>
</template>

<script>
	export default {
	  name: "DetailParamInfo",
	  props: {
	    paramInfo: {
	      type: Object,
	      default() {
	        return {};
	      }
	    }
	  }
	};
</script>

<style lang="scss">
	.param-info {
	  font-size: 14px;
	  padding: 20px 15px;
	  border-bottom: 5px solid #f2f5f8;
	}
	
	.param-info text {
	  font-size: 15px;
	  margin-bottom: 10px;
	}
	
	.param-info table {
	  width: 100%;
	  border-collapse: collapse;
	}
	
	.param-info table tr {
	  height: 42px;
	}
	
	.param-info table tr td {
	  border-bottom: 1px solid rgba(100, 100, 100, 0.1);
	}
	
	.info-param-key {
	  /*当value的数据量比较大的时候, 会挤到key,所以给一个固定的宽度*/
	  width: 95px;
	}
	
	.info-param {
	  border-top: 1px solid rgba(0, 0, 0, 0.1);
	}
	
	.param-value {
	  color: #eb4868;
	}
	
	.info-image image {
	  width: 100%;
	}
</style>
